.callback {
  background-color: #2D323E;
  padding-top: 35px;
  padding-bottom: 34px;
  background-image: url("../img/callback-bg.png");
  background-size: cover;
  margin-bottom: 65px;
}

.callback__wrapper {
  background: #FFFFFF;
  box-shadow: 0px 30px 100px rgba(0, 0, 0, 0.15);
  border-radius: 46px;
  padding-top: 25px;
  padding-bottom: 30px;
  margin-left: 15px;
  margin-right: 15px;
  max-width: 375px;
  @media (min-width: 425px) {
    max-width: 375px;
    margin-left: auto;
    margin-right: auto;
  }
}

.callback__title {
  font-size: 22px;
  line-height: 130%;
  text-align: center;
  margin-bottom: 30px;
}

.callback__form-policy {
  font-size: 8px;
  line-height: 10px;
  color: #858585;
  padding-left: 17px;
  padding-right: 15px;
  display: block;
}

.callback__form-item {
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 13px;
}

.callback__form-input {
  width: 100%;
  border: 1px solid #EFEFEF;
  box-sizing: border-box;
  box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.1);
  border-radius: 7px;
  background: #F7F7F7;
  padding-bottom: 16px;
  padding-top: 16px;
  font-weight: 300;
  font-size: 12px;
  line-height: 14px;
  padding-left: 23px;
  padding-right: 35px;
  color: #000000;
  opacity: 0.5;
}

.callback__form-btn {
  max-width: 100%;
  box-shadow: none;
  border: none;
}

.callback__form-item--name {
  position: relative;

  &:before {
    display: block;
    position: absolute;
    content: '';
    width: 12px;
    height: 15px;
    background-image: url("data:image/svg+xml,%3Csvg width='12' height='15' viewBox='0 0 12 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='0.2' d='M10.6993 10.7222L8.20722 9.52825C7.97217 9.4155 7.82609 9.189 7.82609 8.937V8.09175C7.88583 8.02175 7.9487 7.942 8.01365 7.854C8.33687 7.4165 8.59591 6.9295 8.78426 6.40425C9.15235 6.2425 9.3913 5.89375 9.3913 5.5V4.5C9.3913 4.25925 9.29739 4.026 9.13043 3.84375V2.514C9.14504 2.3765 9.20243 1.558 8.5847 0.88275C8.04887 0.297 7.17939 0 6 0C4.82061 0 3.95113 0.297 3.4153 0.8825C2.79756 1.55775 2.85496 2.3765 2.86957 2.514V3.84375C2.70261 4.026 2.6087 4.25925 2.6087 4.5V5.5C2.6087 5.80425 2.75296 6.088 2.99922 6.27725C3.23817 7.184 3.73826 7.86725 3.91304 8.0865V8.91375C3.91304 9.15575 3.7753 9.37775 3.55383 9.49375L1.22661 10.7102C0.469826 11.106 0 11.8645 0 12.6905V13.5C0 14.6865 3.92478 15 6 15C8.07522 15 12 14.6865 12 13.5V12.7393C12 11.8798 11.5015 11.1067 10.6993 10.7222Z' fill='black'/%3E%3C/svg%3E%0A");
    background-size: cover;
    background-repeat: no-repeat;
    right: 35px;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
  }
}

