  position: relative;

  &:before {
    display: block;
    position: absolute;
    content: '';
    width: 13px;
    height: 13px;
    background-image: url("data:image/svg+xml,%3Csvg width='13' height='13' viewBox='0 0 13 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath opacity='0.2' d='M12.7086 10.2824L10.7009 8.26995C10.301 7.87077 9.63881 7.8829 9.22499 8.2978L8.21353 9.31133C8.14963 9.27603 8.08349 9.23916 8.01395 9.20004C7.37522 8.84533 6.50101 8.35916 5.58109 7.43648C4.65844 6.51185 4.17292 5.63432 3.81792 4.99373C3.78046 4.92586 3.74457 4.86043 3.70913 4.79829L4.38797 4.11893L4.72171 3.78402C5.13616 3.36851 5.14759 2.70498 4.74864 2.30464L2.74099 0.291991C2.34204 -0.107791 1.67952 -0.0956627 1.26507 0.319841L0.699244 0.890203L0.714706 0.905588C0.524978 1.14823 0.366435 1.42807 0.248453 1.72986C0.139695 2.01712 0.0719828 2.29124 0.0410212 2.56592C-0.224076 4.76861 0.780215 6.7817 3.50573 9.51343C7.27322 13.2892 10.3093 13.004 10.4403 12.99C10.7256 12.9559 10.999 12.8875 11.2768 12.7794C11.5753 12.6625 11.8543 12.5039 12.0962 12.3141L12.1086 12.3251L12.6818 11.7625C13.0954 11.3471 13.1073 10.6834 12.7086 10.2824Z' fill='black'/%3E%3C/svg%3E%0A");
    background-size: cover;
    background-repeat: no-repeat;
    right: 35px;
    top: 50%;
